// Weather
#weather {
  color: rgb(253, 242, 242);
  display: inline-block;
  width: 150px;
  height: 150px;
  position: absolute;
  top: 19%;
  left: 0;
  font-size: 1rem;
  text-shadow: 0 0 5px #333;
  transition: 0.3s;
  background-color: rgba(0, 0, 0, 0.623);
  padding: 15px;
  border-radius: 20px;
  box-shadow: 0 0 5px #333;

  span {
    display: block;
    line-height: 1.3;
  }

  .city {
    margin-top: 10px;
  }

  .weather {
    display: inline-block;
  }

  .temperature {
    font-size: 2.5rem;
  }

  .cloud {
    margin-right: 10px;
    transform: translateY(5px);
  }

}